<template>
    <div>
        <div class="shipment-text">出货中{{time}}S</div>
        <img class="shipment-img" src="../../static/images/shipment.png"/>
    </div>
</template>
<script>
export default {
    data () {
        return{
            hasAdvert: false,
            advertUri: '',
            time: 3
        }
    },
    created(){
        this.hasAdvert = this.$route.params.hasAdvert;
        this.advertUri = this.$route.params.advertUri;
        this.timer(this.time);
    },
    methods:{
        timer(t){
            this.time = t-1;
            setTimeout(function(){
                if(this.time > 0){
                    this.timer(this.time);
                }else{
                    if(hasAdvert){
                        window.location.href = advertUri;
                    }else{
                        this.$router.go(1)
                    }
                }
            }, 1000)
        }
    }
}
</script>
<style scoped>
.shipment-img{ width: 30%;}
</style>


